export default "<script context=\"module\">\n  const example = \"UpdatingPropsGlider\"\n</script>\n\n<script lang=\"ts\">\n  import Glider  from '..'\n  import Details from './lib/Details.svelte'\n\n  let slidesToShow: number = 1\n</script>\n\n<div class=\"container\">\n  Number of slides:\n  <select bind:value={slidesToShow}>\n    {#each Array(4) as _, i}\n      <option>{i+1}</option>\n    {/each}\n  </select>\n  <Glider\n    class=\"glider-container\"\n    draggable\n    hasDots\n    slidesToShow={slidesToShow}\n    scrollLock\n  >\n    <div class=\"slide\">\n      <span>1</span>\n    </div>\n    <div class=\"slide\">\n      <span>2</span>\n    </div>\n    <div class=\"slide\">\n      <span>3</span>\n    </div>\n    <div class=\"slide\">\n      <span>4</span>\n    </div>\n    <div class=\"slide\">\n      <span>5</span>\n    </div>\n    <div class=\"slide\">\n      <span>6</span>\n    </div>\n    <div class=\"slide\">\n      <span>7</span>\n    </div>\n    <div class=\"slide\">\n      <span>8</span>\n    </div>\n    <div class=\"slide\">\n      <span>9</span>\n    </div>\n    <div class=\"slide\">\n      <span>10</span>\n    </div>\n    <div class=\"slide\">\n      <span>11</span>\n    </div>\n    <div class=\"slide\">\n      <span>12</span>\n    </div>\n  </Glider>\n  <Details {example} />\n</div>\n"